<template>
    <div class="container">
        <div class="intro">
            <header style="text-align: center;font-size: 1.2rem;">{{title}}</header>
            <p>
                {{ intro }}
            </p>
            <div class="test">
                <mt-button size="large" type="primary" @click.native="handleClick">开始测试</mt-button>
            </div>
        </div>

        <div class="gg">
            广告
        </div>
    </div>

</template>


<script>
    export default {
        data() {
            return {
                title: this.$route.query.title,
                intro: this.$route.query.intro
            }
        },
        methods: {
            handleClick() {
                this.$router.push({ path: '/xlcs', query: { id: this.$route.query.id} });
            }
        }
    }

</script>

<style>
    .container {
        background: #ddd;
        position: relative;
        height: 100%;
    }


    .intro {
        height: 200px;
        padding: 10px;
        background: #fff;
        position: relative;
    }

    .gg {
        background: #fff;
        position: absolute;
        top: 230px;
        bottom: 0;
        left: 0;
        right: 0;
        text-align: center;
        font-size: 48px;
        line-height: 8;
    }

    .test {
        bottom: -2rem;
        position: relative;
    }

</style>


