<template>
    <div>
        <header style="text-align: center;margin-top: 10px;font-size: 1.2rem;">{{data.title}}</header>

        <mt-cell style="margin: 1rem 0;" v-bind:title="data.question"></mt-cell>
        <div class="page-swipe">
            <mt-swipe :showIndicators=false>
                <mt-swipe-item class="slide1"><img height="200px" width="100%" src="../../assets/test.jpg"></mt-swipe-item>
            </mt-swipe>
        </div>
        <div class="choose">
            <!--<p style="white-space:pre-wrap">-->
                <!--{{ data.choose }}-->
            <!--</p>-->
            <mt-radio
                    title=""
                    v-model="value"
                    :options="options">
            </mt-radio>
        </div>
        <div class="answer">
            <mt-button size="large" type="primary" @click.native="handleClick">{{ answerTip }}</mt-button>
        </div>
        <div v-if="show">
            <div class="content">{{ answer }}</div>
        </div>
    </div>
</template>

<script>
    import { getXlcs, context } from '../../api/api';
    import Util from '../../common/js/util.js';

    export default {
        data() {
            return {
                answerTip: '查看答案',
                show: false,
                data: {},
                value: '',
                options: [],
                answer: ''
            }
        },
        methods: {
            handleClick() {
                if (this.show) {
                    this.show = false;
                    this.answerTip = '查看答案';
                } else {
                    this.show = true;
                    this.answerTip = '重新选择';
                    let val = this.value;
                    if (val == 'A') {
                        this.answer = this.data.answera;
                    } else if (val == 'B') {
                        this.answer = this.data.answerb;
                    } else if (val == 'C') {
                        this.answer = this.data.answerc;
                    } else if (val == 'D') {
                        this.answer = this.data.answerd;
                    } else if (val == 'E') {
                        this.answer = this.data.answere;
                    } else if (val == 'F') {
                        this.answer = this.data.answerf;
                    }
                }
            },
            getInfo() {
                let para = {
                    id: this.$route.query.id
                };

                getXlcs(para).then((res) => {
                    let list = res.data.data;
                    this.data = list;
                    let options = [];
                    if (list.choosea !== '') {
                        let option = { label: list.choosea, value: 'A'};
                        options.push(option);
                    }
                    if (list.chooseb !== '') {
                        let option = { label: list.chooseb, value: 'B'};
                        options.push(option);
                    }
                    if (list.choosec !== '') {
                        let option = { label: list.choosec, value: 'C'};
                        options.push(option);
                    }
                    if (list.choosed !== '') {
                        let option = { label: list.choosed, value: 'D'};
                        options.push(option);
                    }
                    if (list.choosee !== '') {
                        let option = { label: list.choosee, value: 'E'};
                        options.push(option);
                    }
                    if (list.choosef !== '') {
                        let option = { label: list.choosef, value: 'F'};
                        options.push(option);
                    }

                    this.options = options;
                });
            },
        },
        mounted() {
            this.getInfo();
        }
    }

</script>

<style>

    .page-swipe .mint-swipe{
        height: 200px;
        color: #fff;
        font-size: 30px;
        text-align: center;
        margin-bottom: 20px;
    }
    .page-swipe .mint-swipe-item {
        line-height: 200px;
    }
    .page-swipe .slide1 {
        background-color: #0089dc;
        color: #fff;
    }
    .page-swipe .slide2 {
        background-color: #ffd705;
        color: #000;
    }
    .page-swipe .slide3 {
        background-color: #ff2d4b;
        color: #fff;
    }
    .choose {
        font-size: 1.2rem;
    }
    .answer {
        padding: 0 10px;
        margin-top: 10px;
    }
    .content {
        font-size: 1rem;
        padding: 10px 4px;
    }

</style>


