import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import XLCS from './views/old/XLCS.vue'
import Test from './views/old/Test.vue'


let routes = [
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/xlcs',
        component: XLCS,
    },
    {
        path: '/',
        component: Home,
    },
    {
        path: '/test',
        component: Test,
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


